import { Component, OnInit } from '@angular/core';
import { EventsService } from '../services/index';

@Component({
    selector: 'foot',
    templateUrl: '../../views/main_partials/foot.html',
    styleUrls: ['../../styles/foot.css'],
})
export class FootComponent implements OnInit {
    
    // admin Dashboard
    public pageType: boolean

    constructor(private eS: EventsService) {}

    ngOnInit() { 

        this.pageType = !/admin/.test(window.location.pathname)?  true : false
    }

    /**
     * routerLink
     */
    public routerLink(link: string): void {

        this.eS.broadcast('routerLink', link)
    }
}


